@mixin responsive($ScreenWidth){
    @media(max-width: $ScreenWidth){
        @content;
    }
}

@mixin style-btn ($heigth, $bg, $colorText)
{

    display: inline-block;
    height: $heigth;
    line-height: 4.5rem ;
    background-color: $bg;
    border-radius: 6rem;
    padding: 0px 5rem;
    font-weight: bold;
    font-size: 1.6rem;
    cursor: pointer;
    transition: all .03s;
    &:hover{
    background-color: $colorText;
    color: $bg;
    }
}